<template>
    <div class="todos">
    <transition-group name="todo">
      <div class="todo" v-for="todo in deletedTodos" :key="todo.id">
        <div>
          <span>{{ todo.group }}</span>
        </div>
        <p>{{ todo.message }}</p>
        <div class="todo-footer">
          <button class="recover" @click="$store.commit('recoverTodo', todo.id)">Восстановить</button>
          <button @click="$store.commit('removeTodo', todo.id)">Удалить</button>
        </div>
      </div>
    </transition-group>
  </div>
</template>

<script>
import dataBasket from './dataBasket';

export default {
    setup()
    {
        return {
            ...dataBasket()
        }
    }
}


</script>