<template>
  <div class="todos">
    <transition-group name="todo">
      <div class="todo" v-for="todo in validTodos" :key="todo.id">
        <div>
          <span>{{ todo.group }}</span>
        </div>
        <p>{{ todo.message }}</p>
        <div class="todo-footer">
          <button @click="$emit('todoRemove', todo.id)">Удалить</button>
        </div>
      </div>
    </transition-group>
  </div>
</template>

<script>
export default {
    props : {
        validTodos : {
            default : []
        }
    },
    emits : {
      'todoRemove' : null
    }
}
</script>