import { createRouter, createWebHashHistory } from 'vue-router';
import AllTodos from '../components/AllTodos.vue';
import Basket from '../components/Basket.vue';
import Search from '../components/Search.vue';
import Groups from '../components/Groups.vue';
import Group from '../components/Group.vue';

const routes = [
  {path : '/', component : AllTodos, name : 'home'},
  {path : '/:pathMatch', component : AllTodos},
  {path : '/basket', component : Basket, name : 'basket'},
  {path : '/search', component : Search, name : 'search'},
  {path : '/groups', component : Groups, name : 'groups'},
  {path : '/groups/:id', component : Group, name : 'group'}
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})


router.beforeEach((to, from) =>
{
  if (to.name === 'group') {
    let rec = JSON.parse(localStorage.getItem('groups'));
    if (!rec.find(x => x.message === to.params.id)) {
      return from.path;
    }
  }
})

export default router
