import { useRoute } from 'vue-router';
import { useStore } from 'vuex';
import { ref, computed } from 'vue';

export default () =>
{
    const store = ref(useStore());
    const route = ref(useRoute());

    const validTodos = computed(() =>
    {
        return store.value.getters.allTodos.filter(x => x.group === route.value.params.id && !x.deleted).reverse();
    })

    return {
        validTodos
    }
}