import { ref, computed } from 'vue';
import { useStore } from 'vuex';

export default () =>
{
    const store = ref(useStore());

    let message = ref("");

    const validTodos = computed(() =>
    {
        if (!message.value.length) {
            return [];
        }

        let text = message.value.toUpperCase();
        return store.value.getters.allTodos.filter(x => (`${x.group.toUpperCase()} ${x.message.toUpperCase()}`.includes(text) && !x.deleted));
    })

    return {
        validTodos,
        message
    }
}