import { createStore } from 'vuex';

const store = {
    state : {
        todos : localStorage.getItem('todos')? JSON.parse(localStorage.getItem('todos')) : [],
        groups : localStorage.getItem('groups')? JSON.parse(localStorage.getItem('groups')) : []
    },
    actions : {

    },
    mutations : {
        addTodo(state, param)
        {
            state.todos.push({...param});
            localStorage.setItem('todos', JSON.stringify(state.todos));
        },
        removeTodo(state, id)
        {
            let rec = state.todos.find(x => x.id === id);

            if (rec.deleted) {
                state.todos = state.todos.filter(x => x.id !== id);
            }

            else {
                rec.deleted = true;
            }
            localStorage.setItem('todos', JSON.stringify(state.todos));
        },
        recoverTodo(state, id)
        {
            state.todos.find(x => x.id === id).deleted = false;
            localStorage.setItem('todos', JSON.stringify(state.todos));
        },
        addGroup(state, param)
        {
            state.groups.push(param);
            localStorage.setItem('groups', JSON.stringify(state.groups));
        },
        removeGroup(state, data)
        {
            const [id, group] = [...data];
            state.groups = state.groups.filter(x => x.id !== id);
            state.todos = state.todos.filter(x => x.group !== group);
            localStorage.setItem('groups', JSON.stringify(state.groups));
            localStorage.setItem('todos', JSON.stringify(state.todos));
        }
    },
    getters : {
        allTodos(state)
        {
            return state.todos;
        },
        allGroups(state)
        {
            return state.groups;
        }
    }
};

export default new createStore(store);