<template>
    <div class="all-todos">
        <app-todos :validTodos="validTodos" @todoRemove="$store.commit('removeTodo', $event)"></app-todos>
        <button class="add" @click="openPopap">+</button>
        <transition name="popup">
            <app-popup ref="popap"></app-popup>
        </transition>
    </div>
</template>

<script>
import Popap from './Popap.vue';
import dataTodos from './dataTodos';
import Todos from './Todos.vue';

export default {
    components : {
        'app-popup' : Popap,
        'app-todos' : Todos
    },
    methods : {
        async openPopap()
        {
            await this.$refs.popap.open().then(x =>
            {
                this.$store.commit('addTodo', {...x});
            }).catch(() => {
                return;
            });
        }
    },
    setup()
    {
        return {
            ...dataTodos()
        }
    },
    props : {
        todos : {
            default : []
        }
    },
    emits : {
        'onChangeTodos' : null,
        'todoRemove' : null
    }
}


</script>