<template>
    <div>
        <app-todos :validTodos="validTodos" @todoRemove="$store.commit('removeTodo', $event)"></app-todos>
        <button class="add" @click="openPopap">+</button>
        <transition name="popup">
            <app-popup ref="popup" :place="'Написать заметку'"></app-popup>
        </transition>
    </div>
</template>

<script>
import Todos from './Todos.vue';
import dataGroup from './dataGroup.js';
import groupPopap from './groupPopap.vue';

export default {
    setup() {
        return { 
            ...dataGroup()
        }
    },
    components : {
        'app-todos' : Todos,
        'app-popup' : groupPopap
    },
    methods : {
        async openPopap()
        {
            await this.$refs.popup.open().then(x =>
            {
                let data = {group : this.$route.params.id, deleted : false, ...x};
                this.$store.commit('addTodo', data);
            }).catch(() => {return;});
        }
    }
}


</script>