<template>
  <div class="popap-parent" v-show="isPopapOpen">
    <div class="popap-body">
      <div class="input-parent">
        <textarea placeholder="Написать заметку" v-model="message"></textarea>
      </div>
      <select v-model="group">
        <option value="Без группы">Без группы</option>
        <option :value="group.message" v-for="group in $store.getters.allGroups" :key="group.id">{{ group.message }}</option>
      </select>
      <div class="popap-footer">
        <button @click="close(false)" class="error">Отмена</button>
        <button :disabled="!message.length" :class="{'disabled' : !message.length}"
        @click="close({message, group, deleted : false, id : new Date()})">Добавить</button>
      </div>
    </div>
  </div>
</template>

<script>

export default {
    resolve : null,
    reject : null,
    data()
    {
        return {
            message : '',
            group : 'Без группы',
            isPopapOpen : false
        }
    },
    methods : {
        open()
        {
            return new Promise((res, rej) => {
                this.isPopapOpen = true;
                this.$options.resolve = res;
                this.$options.reject = rej;
            })
        },
        close(param)
        {
            if (!param) {
                this.$options.reject();
            }

            else {
                this.$options.resolve(param);
            }

            this.message = '';
            this.isPopapOpen = false;
        }
    }
}

</script>