<template>
  <div class="todos">
    <transition-group name="todo">
        <div class="group" v-for="group in allGroups" :key="group.id">
            <span>{{ group.message }}</span>
            <div class="todo-footer">
                <button class="recover" @click="$router.push({name : 'group', params : {id : group.message}})">Открыть</button>
                <button @click="$store.commit('removeGroup', [group.id, group.message])">Удалить</button>
            </div>
        </div>
    </transition-group>
    <button class="add" @click="openPopap">+</button>
    <transition name="popup">
      <app-popup ref="popup"></app-popup>
    </transition>
  </div>
</template>

<script>
import Popap from "./groupPopap.vue";

export default {
  components: {
    "app-popup": Popap,
  },
  methods: {
    async openPopap() {
      await this.$refs.popup
        .open()
        .then((x) => {
          this.$store.commit("addGroup", x);
        })
        .catch(() => {
          return;
        });
    }
  },
  computed: {
    allGroups() {
      return this.$store.getters.allGroups;
    },
  }
};
</script>