<template>
    <div class="search">
        <div class="search-head">
            <input type="text" placeholder="Введите текст или группу" v-model="message">
        </div>
        <transition name="popup" mode="out-in">
            <div class="not-found" v-if="!validTodos.length && message.length">
                Нет результатов
            </div>
            <app-todos v-else :validTodos="validTodos" @todoRemove="$store.commit('removeTodo', $event)"></app-todos>
        </transition>
    </div>
</template>

<script>
import Todos from './Todos.vue';
import dataSearch from './dataSearch';

export default {
    setup() {
        return {
            ...dataSearch()
        }
    },
    components : {
        'app-todos' : Todos
    }
}


</script>