<template>
  <div class="popap-parent" v-show="isPopapOpen">
    <div class="popap-body" style="height : 200px">
      <div class="input-parent">
        <textarea :placeholder="place" v-model="message"></textarea>
      </div>
      <div class="popap-footer">
        <button @click="close(false)" class="error">Отмена</button>
        <button
          :disabled="!message.length"
          :class="{ disabled: !message.length }"
          @click="close({ message, id: new Date() })"
        >
          Добавить
        </button>
      </div>
    </div>
  </div>
</template>

<script>


export default {
    resolve : null,
    reject : null,
    data()
    {
        return {
            message : '',
            isPopapOpen : false
        }
    },
    methods : {
        open()
        {
            return new Promise((res, rej) =>
            {
                this.isPopapOpen = true;
                this.$options.resolve = res;
                this.$options.reject = rej;
            })
        },
        close(param)
        {
            if (!param) {
                this.$options.reject();
            }

            else {
                this.$options.resolve(param);
            }

            this.message = '';
            this.isPopapOpen = false;
        }
    },
    props : {
      place : {
        type : String,
        required : false,
        default : "Создать группу"
      }
    }
}


</script>