import { ref } from 'vue';
import { useRouter } from 'vue-router';

export default () =>
{
    const router = ref(useRouter());

    let openMenu = ref(false);

    const updatePage = data =>
    {
        openMenu.value = false;
        router.value.push(data);
    }

    return {
        openMenu,
        updatePage
    }
}